import uniRate from '../../components/uni-ui/uni-rate/uni-rate.vue'
import artistmodel from '../../components/moviedetail/artistmodel.vue'
export default {
	name: "",
	components: {
		uniRate,
		artistmodel
	},
	props: {},
	data() {
		return {
			// 接收传过来的id值
			movieId: '',
			// 接收请求出来的数据
			movieObj: {},
			// 接收请求出来的导演数据
			directorArr: [],
			// 接收请求出来的演员数据
			actorArr: [],
			// 将剧照从请求出来的电影数据中取出来
			photosArr: []
		}
	},
	methods: {
		getMovieDetail() {
			uni.request({
				url: this.$api.goToDetail(this.movieId),
				method: 'POST',
				data: {},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					this.movieObj = res.data.data
					this.photosArr = JSON.parse(res.data.data.plotPics)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 请求导演数据和演员数据
		getArtists(types) {
			uni.request({
				url: this.$api.getArtist(this.movieId, types),
				method: 'POST',
				data: {},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					if (types === 1) {
						this.directorArr = res.data.data
					} else {
						this.actorArr = this.directorArr.concat(res.data.data)
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 跳转图片预览
		goToCover(src) {
			uni.previewImage({
				urls: [src],
				longPressActions: {
					itemList: ['保存图片到本地', '取消'],
					success: function(data) {
						console.log(data);
						// if() {

						// }
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			})
		}
	},
	mounted() {
		if (this.movieId !== '') {
			this.getMovieDetail()
			this.getArtists(1)
			setTimeout(() => {
				this.getArtists(2)
			}, 100)
		}
	},
	onLoad(option) {
		this.movieId = option.id
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
